<template>
  <div class="">
    <el-button type="primary" @click="getArticle">调用后台接口</el-button>
    <el-input v-model="inlist.ID" style="width: 200px;"></el-input>
    <el-input v-model="inlist.Name" style="width: 200px;"></el-input>
    <el-button type="primary">test</el-button>
    <p></p>
    <el-table ref="multipleTable"
              :data="list"
              tooltip-effect="dark"
              border fit highlight-current-row style="width: 100%;"
              row-key="id">
      <!--<el-table-column width="65" align="center" label="序号" prop="index"></el-table-column>-->
      <el-table-column width="center" align="center" label="ID" prop="ID"></el-table-column>
      <el-table-column width="center" align="center" label="Name" prop="Name"></el-table-column>
      <el-table-column width="center" align="center" label="CountryCode" prop="CountryCode"></el-table-column>
      <el-table-column align="center" label="District" prop="District"></el-table-column>
      <el-table-column align="center" label="Population" prop="Population"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'blog',
  data () {
    return {
      inlist: {
        ID: null,
        Name: null
      },

      list: []
    }
  },
  created () {
    this.getArticle()
  },
  methods: {
    getArticle () {
      this.$http.get('http://192.168.1.122:3000', {params: this.inlist}).then((res) => {
        console.log(res)
        console.log(res.data)
        console.log(res.data.data)
        this.list = res.data
      })
    }
  }
}
</script>

<style scoped>

</style>



